@use './../../../base';

.formControl,
.textareaVariant {
  display: inline-block;
  padding: 10px;
  border: base.$border-line-color;
  box-shadow: base.$box-shadow;
  background-color: base.$background-color-form;
  border-radius: 8px;
  position: relative;
  word-wrap: break-word;
  textarea {
    resize: none;
    width: 100%;
    height: 100%;
    outline: none;
    border: transparent;
    overflow: hidden;
    white-space: pre-wrap;
    word-wrap: break-word;
    background-color: base.$background-color-form;
  }
  &.error {
    border: 1px solid rgb(255, 0, 0);
    box-shadow: 0 0 5px rgba(255, 0, 0, 0.5);
  }
}

.formControl {
  width: 300px;
  min-height: 70px;
}

.textareaVariant {
  width: 95%;
  min-height: 20px;
}

.inputControl {
  width: 250px;
  height: 20px;
  line-height: 20px;
  padding: 10px;
  border: base.$border-line-color;
  box-shadow: 0 0 5px rgba(52, 151, 151, 0.5);
  background-color: base.$background-color-form;
  border-radius: 5px;

  position: relative;
  input {
    resize: none;
    width: 100%;
    color: rgb(8, 32, 32);
    height: 100%;
    outline: none;
    border: transparent;
    overflow: auto;
    white-space: pre-wrap;
    background-color: base.$background-color-form;
  }
  &__cross {
    position: absolute;
    top: 10px;
    right: 10px;
    color: red;
    // visibility: hidden;
  }

  &__errorText {
    position: absolute;
    color: red;
    bottom: -18px;
    left: 10px;
    font-size: 10px;
    // visibility: hidden;
  }
  &.error {
    border: 1px solid red;
    // .formControl__cross {
    // 	visibility: visible;
    // }
    // .formControl__error {
    // 	visibility: visible;
    // }
  }
}

.checkboxControl {
  border-radius: 5px;
  display: inline;
  margin: 0px 5px 0px 0px;
  input {
    border: transparent;
  }
}
